.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.courses_content {
    padding: 3.2rem;
    border-width: 1px;
    border-style: solid;
    border-color: #d1d7dc;
    width: 60%;
    margin: auto;
}

.courses_content_descreption h2 {
    margin-bottom: 0.8rem;
    line-height: 1.2;
    letter-spacing: -.02rem;
    font-size: 1.8rem;
    font-weight: 700;
}

.courses_content_descreption p {
    width: 80%;
    margin-bottom: 1.6rem;
    font-weight: 400;
    line-height: 1.4;
    font-size: 1.2rem;
}

.courses_content_descreption {
    margin-bottom: 3.2rem;
}

.explore {
    text-decoration: none;
    border-style: solid;
    border-width: 1px;
    color: black;
    height: 4rem;
    position: relative;
    align-items: center;
    display: inline-flex;
    min-width: 8rem;
    padding: 0 1.2rem;
    justify-content: center;
    white-space: nowrap;
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: -.02rem;
    font-size: 1rem;
    margin-top: 1.6rem;
}

.explore:hover {
    background-color: rgba(0, 0, 0, .04);
}

.courses_cards {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1.6rem;
    gap: 1.6rem;
}

@media screen and (max-width: 600px) {
    .courses-header {
        display: none;
    }
    .courses_content_descreption {
        display: none;
    }
    .courses_cards {
        justify-content: center;
    }
}
.course-item {
    width: 22%;
    display: inline-flex;
    flex-direction: column;
    cursor: pointer;
}

.course-item h3 {
    font-size: 1rem;
    margin-top: 0.8rem;
    margin-bottom: 0.4rem;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.rating {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 0rem 0rem;
    height: auto;
}

.rating p {
    font-size: 0.8rem;
    margin: 0rem 0.2rem;
}

.rating-score {
    font-size: 0.9rem !important;
}

.rating-score,
.stars {
    color: orange;
}

.author {
    font-size: 0.8rem;
    color: grey;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin: 0.2rem 0.2rem;
}

.rating-score {
    color: #d67c0e;
    font-weight: 700;
    margin-right: 0.4rem;
    font-size: 1.4rem;
}

.rating-n {
    margin-left: 0.4rem;
    font-size: 1.2rem;
    color: #6a6f73;
}

.stars {
    align-items: center;
    font-size: 1rem;
}

.price {
    font-size: 1rem;
    font-weight: 700;
    margin: 0rem 0rem;
}

@media only screen and (max-width: 1080px) {
    .course-item {
        width: 30%;
    }
    .courses_content_descreption p {
        font-size: 1rem !important;
    }
    .courses_content_descreption h2 {
        font-size: 1.5rem !important;
    }
    .explore {
        font-size: 1rem !important;
    }
}

@media only screen and (max-width: 800px) {
    .course-item {
        width: 45%;
    }
}

@media only screen and (max-width: 500px) {
    .course-item {
        width: 100%;
    }
}
